import React, { Component } from 'react'
import geo from '../helper/geo'
import maps from '../helper/maps'
import places from '../helper/places'
import Place from './Place';

export default class Location extends Component {

  constructor(props) {
    super(props)
    this.state = {
      places: [],
      isFetching: true
    }
  }

  async componentDidMount() {
    const { latitude, longitude } = await geo()
    const results = await maps(latitude, longitude)
    this.setState({
      places: places(results),
      isFetching: false
    })
  }

  render() {
    const { places, isFetching } = this.state
    if (places.length === 0) {
      return isFetching
      ? <div>
          <span role="img" aria-label="earth">🌍</span>
          Retrieving location…
        </div>
      : <div>
        <span role="img" aria-label="smiley">🧐</span>
        Sorry. No places found.
      </div>
    }
    return (
      <ul className="places">
        {places.map((props) => <Place key={props.url} {...props} />)}
      </ul>
    )
  }

}



